.container{
  display: flex;
  align-items: flex-start;
  gap: 6.25rem;
/*  padding: 7.5rem 5.875rem;*/
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: #F4F4F4;

/*  padding: 2% 18%;
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1.5px solid #000000;
  background-color: #F6F3F5;
  color: #000000;*/
}

#myName{
  display: flex;
  height: 36.9375rem;
  width: 17.5rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

#header{
  font-family: 'Xanh Mono', monospace;
  font-style: italic;
  font-weight: 400;
  color: #000;
  width: auto;
  font-size: 3.5rem;
  line-height: 88%; /* 3.08rem */
}

#pics{
  width: 17.5rem;
  height: 17.5rem;
  flex-shrink: 0;

  background: lightgray -35.401px -125.76px / 120% 160% no-repeat;
  mix-blend-mode: luminosity;
}

#pics:hover{
  mix-blend-mode: normal;
}

#tt1{
  display: flex;
  width: auto;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.125rem;
}

#t{
  color: #000;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
}

#b1{
  color: #000;
  font-family: Xanh Mono;
  font-size: 1.5rem;
  font-style: italic;
  font-weight: 400;
  line-height: normal;
}

#contents{
  display: flex;
  width: 44.5rem;
  flex-direction: column;
  align-items: flex-start;
  gap: 2.5rem;
}

#tt2{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.25rem;
  align-self: stretch;
}

#b2{
  color: #000;
  font-family: Xanh Mono;
  font-size: 2.25rem;
  font-style: italic;
  font-weight: 400;
  line-height: normal;
}

#links{
  display: flex;
  flex-wrap: wrap;
}


html, body, #root, .App{
  /*height: 100%;*/
}

.hiddenimg{
  display: none;
}

@media screen and (max-width: 1250px) {
  .container{
    flex-wrap: wrap;
    padding: 7.5rem 5.875rem;
    height: auto;

  }
    #b2{
    font-size: 1.75rem;
  }
  #contents{
    min-width: 17.5rem;
  }
}

@media screen and (min-width: 650px) {
  html, body, #root, .App{
  height: 100%;
  }
}

@media screen and (min-width: 801px) {
  .caseify{
    padding: 5rem 20%;
  }
}

@media screen and (max-width: 800px){
  .caseify{
    padding: 5rem 2rem;
  }
  #tablecol{
    flex-direction: column;
  }
}

a:hover{
  cursor: url(https://64.media.tumblr.com/tumblr_m2wjs66FWY1qfamg6.gif), move;
  background-color: #2E2E2E;
  color: #F4F4F4!important;
}

 a:link, a:visited, a:active #button{
  text-decoration: none;
}

/* case study */

.caseify{
  padding-bottom: 8rem;
  height: 100;
  background: #F4F4F4;
  display: flex;
  flex-direction: column;
/*  justify-content: center;*/
/*  align-items: center;*/
  gap: 2rem;
  flex-shrink: 0;
}

#casetitle{
  color: #000;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  align-self: stretch;
}

#casetext, p, u, p2, i{
  color: #000;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
/*  align-self: stretch;*/
  margin-top: 0em;
}

p2{
  margin-bottom: 0em;
}

i{
  font-style: italic;
}

#art{
  width: 100%;
  height: auto;
}

#table{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
  align-self: stretch;
}

#tablecol{
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  align-self: stretch;
}

#tablerow{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: 1 0 0;
}

#back{
  color: #000;
  font-family: Xanh Mono;
  font-size: 1.25rem;
  font-style: italic;
  font-weight: 400;
  line-height: normal;
}

#title{
  color: #000;
  font-family: Xanh Mono;
  font-size: 1.75rem;
  font-style: italic;
  font-weight: 400;
  line-height: normal;
}
